@mixin tablet {
  @media screen and (min-width: 768px) {
    @content;
  }
}

@mixin desktop {
  @media screen and (min-width: 1200px) {
    @content;
  }
}

@mixin button-design {
  background-color: $primary-color;
  color: white;
  font-family: "Oswald";
  font-weight: 500;
  border-radius: 20px;
  font-size: 1rem;
  box-sizing: border-box;
  border: none;
  outline: none;
  cursor: pointer;
}
